@import url(https://fonts.googleapis.com/css?family=Open+Sans:300, 600);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}
@media only screen and (min-width: 1800px) {
  html {
    font-size: 75%;
  }
}
@media only screen and (max-width: 1200px) {
  html {
    font-size: 55.25%;
  }
}
@media only screen and (max-width: 900px) {
  html {
    font-size: 42.5%;
  }
}

body {
  font-size: 1.4rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #121212;
}

.app {
  position: relative;
}

.sorry-text {
  padding: 4rem 0;
  text-align: center;
  font-size: 2rem;
  font-weight: 300;
  letter-spacing: 1.4px;
}

::selection {
  background: none;
}

.error-tooltip{position:fixed;bottom:0px;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);color:rgba(255,255,255,0.8);background-color:rgba(0,0,0,0.4);padding:6px 12px;font-size:1.4rem;text-align:center;border:1.4px solid rgba(255,0,0,0.65);border-radius:4px;opacity:0;-webkit-animation:hide-error 2s forwards;animation:hide-error 2s forwards;z-index:4000}@-webkit-keyframes hide-error{0%{bottom:0px;opacity:0}30%{bottom:10px;opacity:1}60%{bottom:10px;opacity:1}100%{bottom:20px;opacity:0}}@keyframes hide-error{0%{bottom:0px;opacity:0}30%{bottom:10px;opacity:1}60%{bottom:10px;opacity:1}100%{bottom:20px;opacity:0}}

.phone-search__backdrop{display:flex;justify-content:center;align-items:center;position:absolute;left:0;top:0;height:10rem;width:100%;background:#1b1b1b;z-index:100}.phone-search__form{display:flex;justify-content:space-around;align-items:center;width:100%;padding:4rem}.phone-search__form__input{font-size:1.6rem;color:#2a2a2a;width:100%;padding:1rem 4.8rem 1rem 2rem;background:#e6e6e6;border:none;border-radius:100px;-webkit-transition:all 0.2s;transition:all 0.2s}.phone-search__form__input:focus{outline:none}.phone-search__form__btn{background:transparent;border:none;display:flex;position:absolute;right:5rem;justify-content:center;align-items:center;cursor:pointer}.phone-search__form__btn:focus,.phone-search__form__btn:active{border:none;outline:none}

.profile{display:flex;flex-direction:column}.profile .profile-btn{width:4rem;height:4rem;border:none;cursor:pointer}.profile .profile-btn:focus,.profile .profile-btn:active{outline:none}.profile .profile-btn__img{width:4rem;height:4rem;border-radius:10rem}.profile .profile-btn__list{display:flex;flex-direction:column;position:absolute;top:7rem;right:4rem;width:14rem;margin-right:4rem;border:1px solid rgba(255,255,255,0.5)}@media only screen and (max-width: 600px){.profile .profile-btn__list{width:15rem;right:0rem}}.profile .profile-btn__list__favorite,.profile .profile-btn__list__watch,.profile .profile-btn__list__logout{font-size:1.4rem;padding:1rem 2rem;width:100%;background:#353535;color:#fff;border:none;text-transform:uppercase;cursor:pointer;-webkit-transition:all 0.2s;transition:all 0.2s}.profile .profile-btn__list__favorite:focus,.profile .profile-btn__list__favorite:active,.profile .profile-btn__list__watch:focus,.profile .profile-btn__list__watch:active,.profile .profile-btn__list__logout:focus,.profile .profile-btn__list__logout:active{outline:none}.profile .profile-btn__list__favorite:hover,.profile .profile-btn__list__watch:hover,.profile .profile-btn__list__logout:hover{background:#3a3a3a}.profile .profile-btn__list__logout{border-bottom:none;background:#212121}.login{color:#fff;text-decoration:none}

.search{display:flex;flex-direction:row;align-items:center;position:relative}.search__input{width:0;font-size:1.6rem;color:#2a2a2a;border:none;border-radius:100px;-webkit-transition:all 0.2s;transition:all 0.2s}.search__input:focus{outline:none}.search__btn{background:transparent;border:none;display:flex;justify-content:center;align-items:center;cursor:pointer}.search__btn:focus,.search__btn:active{border:none;outline:none}

.header{display:flex;flex-direction:row;justify-content:space-between;line-height:3rem;vertical-align:middle;padding:3rem 8rem;position:fixed;width:100%;top:0;left:0;background:transparent;z-index:4000;-webkit-transition:all 0.4s;transition:all 0.4s}@media only screen and (max-width: 600px){.header{padding:3rem 4rem}}.header__left{display:flex;flex-direction:row;list-style:none;align-items:center}.header__left>*{padding-right:3rem}.header__left__brand a{display:inline-block;text-transform:uppercase;text-decoration:none;font-weight:700;font-size:2rem;letter-spacing:2px;color:#ff1e1e}.header__left__browse a{text-decoration:none;text-transform:capitalize;color:#eee;letter-spacing:1px}.header__right{list-style:none}

.footer{display:flex;justify-content:space-between;width:100%;background:#212121;padding:6rem 8rem}@media only screen and (max-width: 600px){.footer{padding:6rem 4rem}}.footer__brand{display:flex;flex-direction:column;justify-content:space-between}.footer__brand__name{color:#ff1e1e;font-size:2rem;text-transform:uppercase;letter-spacing:3px;font-weight:700}.footer__brand__author__name{font-size:1.6rem;letter-spacing:2px;text-transform:capitalize;padding-bottom:1rem;font-weight:700}.footer__brand__author__name span{font-size:1.5rem;text-transform:lowercase;font-weight:300;letter-spacing:0.5px}.footer__brand__author__social__portfolio{margin-right:4rem}.footer__support__img{height:10rem}

.loading{height:100vh;background:#212121}.spinner-container{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.spinner{width:80px;height:80px;position:relative;margin:100px auto}.double-bounce1,.double-bounce2{width:100%;height:100%;border-radius:50%;background-color:#505050;opacity:0.6;position:absolute;top:0;left:0;-webkit-animation:sk-bounce 2s infinite ease-in-out;animation:sk-bounce 2s infinite ease-in-out}.double-bounce2{-webkit-animation-delay:-1s;animation-delay:-1s}@-webkit-keyframes sk-bounce{0%,100%{-webkit-transform:scale(0)}50%{-webkit-transform:scale(1)}}@keyframes sk-bounce{0%,100%{transform:scale(0);-webkit-transform:scale(0)}50%{transform:scale(1);-webkit-transform:scale(1)}}

